.fade-enter {
  opacity: 0;
  transform: scale(0.1);
}

.fade-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: all 1000ms linear;
}

.fade-exit {
  opacity: 1;
  transform: scale(1);
}

.fade-exit-active {
  opacity: 0;
  transform: scale(0.1);
  transition: all 1000ms linear;
}
